* {
    background-color: #d7f6ff;
  }
  // styling for inital/add new pokemon table
  table {
    width: 100%;
    font-family: 'Press Start 2P', cursive;
    font-size: 12px;
    text-transform: capitalize;
  }
  
  th {
    text-align: center;
    background-color: #006EB6;
    color: #FFCC01;
    padding: 10px;
    border-radius: 6px;
    border: solid 2px #FFCC01;
    font-family: 'Russo One', sans-serif;
    font-size: 15px;
  }
  
  ul {
    list-style-type: none;
  }
  
  th, td {
    border-bottom: 1px solid;
    text-align: center;
  }
  
  .search_five_randos table{
    display: flex;
    align-items: center;
  }
  
  // add new pokemon section styling
  h1 {
    font-family: 'Pokemon Solid', sans-serif;
    color: #FFCC01;
    -webkit-text-stroke-width: 2px;
      -webkit-text-stroke-color: #006EB6;
    font-size: 50px;
    text-align: center;
    padding-top: -10px;
  }
  
  .pbImg{
    height: 100%;
    width: 30px;
    margin-left: 10px;
    margin-top: 10px;   
  }
  
  h2 {
    text-align: center;
    color: #006EB6;
    font-family: 'Russo One', sans-serif; 
    margin-right: 3px;
  }
  p {
    text-align: center;
    color: red;
  }
  
  section {
    text-align: center;
    margin-right: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
      button {
        margin: 2px;
        margin-top: 2px;
        font-family: 'Russo One', sans-serif;
      }
  }
  
    // button styling
  
  button {
    border-radius: 10px;
